.tournament-standing {
	overflow: auto;
	flex: 1;
	display: flex;
	flex-direction: column;

	&--filled{
		overflow: auto;
		flex: 1;
		display: flex;
		flex-direction: column;
		background-color: var(--palette-live-grey-12);
	}

	&__level {
		color: var(--palette-live-grey-2);
		font-size: var(--font-size-21);
		font-weight: var(--font-weight-medium);
		margin: 10px 15px 10px 15px;
	}
}