.tournament-participants {
	overflow: auto;
	flex: 1;
	display: flex;
	flex-direction: column;
	position: relative;

	&__wrapper {
		flex: 1;
		margin: 0 0 20px 0;

		.tournament-participants__title {
			font-size: var(--font-size-18);
			color: var(--palette-live-grey-0);
			font-weight: var(--font-weight-semi-bold);
			padding: 16px;
			border-bottom: 1px solid var(--palette-live-grey-7);
	
			.tournament-participants__count {
				margin: 0 10px 0 0;
			}
		}
	
		.tournament-participants__button-add {
			margin: 10px 16px 0 16px;
			display: flex;
			justify-content: center;
		}

		.tournament-participants__alert {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-17);
			padding: 20px 16px;
			margin: 16px;
			font-weight: var(--font-weight-regular);
			background-color: var(--palette-live-grey-12);
			border-radius: 11px;
			text-align: center;
		}
	
		.tournament-participants__list-wrapper {
			border: 1px solid var(--palette-live-grey-7);
			border-radius: 16px;
			margin: 20px 16px 0 16px;
	
			.tournament-participants--singles {
	
				.tournament-participants__item {
					align-items: center;
					border-bottom: 1px solid var(--palette-live-grey-11);
					display: flex;
					padding: 12px 16px;
	
					&:last-child {
						border-bottom: 0;
					}
	
					.tournament-participants__number {
						align-items: center;
						border-radius: 100%;
						display: flex;
						font-size: var(--font-size-18);
						color: var(--palette-live-grey-0);
						font-weight: var(--font-weight-regular);
						justify-content: center;
						margin: 0 16px 0 0;
					}
	
					.tournament-participants__name {
						font-size: var(--font-size-18);
						font-weight: var(--font-weight-regular);
						color: var(--palette-live-grey-0);
						margin: 0 0 0 0;
						flex: 1;
					}
	
					.button-delete__icon {
						width: 28px;
						height: 28px;
						padding: 4px;
						border-radius: 100px;
						cursor: pointer;
					}
	
					.tournament-participants__names {
						flex: 1;
	
						.tournament-participants__palyer--first {
							font-size: var(--font-size-18);
							font-weight: var(--font-weight-regular);
							color: var(--palette-live-grey-0);
							margin: 0 0 5px 0;
						}
	
						.tournament-participants__palyer--second {
							font-size: var(--font-size-18);
							font-weight: var(--font-weight-regular);
							color: var(--palette-live-grey-0);
							margin: 0 0 0 0;
						}
					}
				}
			}
		}
	}

	.tournament-participants__button-save {
		box-sizing: border-box;
		position: fixed;
		padding: 0 15px;
		bottom: 80px;
		left: 0px;
		right: 0px;
		display: flex;
		justify-content: center;
		background-color: transparent;
		z-index: 9999;
	}

	.tournament-participants__warning {
		padding: 0 15px;

		p {
			margin: 0 0 20px 0;
		}

		&-buttons{
			display: flex;
			align-items: center;
			justify-content: space-between;

			button:first-child{
				margin-right: 5px;
			}

			button:last-child{
				margin-left: 5px;
			}
		}
	}
}