.search {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	padding: 15px 15px 15px 15px;
	border-radius: 12px;
	background-color: var(--palette-live-grey-8);

	.loupe-icon {
		width: 22px;
		height: 22px;
		margin-right: 15px;
		fill: var(--palette-live-grey-6);
	}

	&__input {
		border: none;
		font-size: var(--font-size-18);
		width: 100%;
		background-color: var(--palette-live-grey-8);
		outline: none;
		padding: 0;
	}
}