.tournament-menu {
	background-color: var(--palette-live-bg-0-white);
	padding: 16px 16px 16px 16px;
	border-top: 1px solid var(--palette-live-grey-6);

	&__wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
		min-height: 25px;

		.tournament-menu__arrow {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			width: 20px;
			right: -2px;
			background-color: var(--palette-live-bg-0-white);
			height: 100%;
		}
	}

	.tournament-menu__footer {
		display: flex;
		justify-content: space-between;
		padding-top: 10px;

		&--open {
			display: flex;
			justify-content: space-between;
			margin: 20px 0 0 0;
		}
	}

	.tournament-menu__link {
		cursor: pointer;
	}
}