.stages-menu {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	position: relative;

	&__wrapper {
		display: flex;
		justify-content: left;
		align-items: center;

		&.keen-slider{
			overflow: hidden;
			margin: 0 28px 0 0;
		}

		.stages-menu__button--inactive {
			background-color: var(--palette-live-bg-0-white);
			color: var(--palette-live-grey-7);
			border: 1px solid var(--palette-live-grey-7);
			height: 44px;
			border-radius: 100px;
			width: 44px;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: var(--font-size-22);
			font-weight: var(--font-weight-semi-bold);
			margin: 0 10px 0 0;
			cursor: pointer;
		}

		.stages-menu__button--active {
			border: 1px solid var(--palette-live-blue);
			background-color: var(--palette-live-blue);
			color: var(--palette-live-bg-0-white);
			height: 44px;
			border-radius: 100px;
			width: 44px;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: var(--font-size-23);
			margin: 0 16px 0 0;
			cursor: pointer;
			font-weight: var(--font-weight-semi-bold);
		}

		.stages-menu__item--active {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 10px 0 0;

			.stages-menu__item-options {
				border: 1px solid var(--palette-live-grey-7);
				padding: 3px 3px 3px 3px;
				border-radius: 100px;
				display: flex;
				justify-content: space-between;
				align-items: center;

				&-button {
					border: 1px solid var(--palette-live-blue);
					background-color: var(--palette-live-blue);
					color: var(--palette-live-bg-0-white);
					height: 36px;
					border-radius: 100px;
					width: 36px;
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: var(--font-size-22);
					font-weight: var(--font-weight-semi-bold);
					cursor: pointer;
					margin: 0 15px 0 0;
				}

				&-icon--active {
					margin: 0 15px 0 0;
					cursor: pointer;
					fill: var(--palette-live-blue);
				}

				&-icon {
					margin: 0 15px 0 0;
					cursor: pointer;
					fill: var(--palette-live-grey-7);
				}
			}
		}
	}

	&__block {
		align-items: center;
		display: flex;
		position: relative;
		right: 24px;

		&-line{
			position: absolute;
			width: 1px;
			height: 65px;
			right: 45px;
			z-index: 100;
			top: -17px;
			background-color: var(--palette-live-grey-6);
		}

		&-button-add {
			align-items: center;
			background-color: var(--palette-live-bg-0-white);
			border: 1px solid var(--palette-live-grey-7);
			border-radius: 100px;
			color: var(--palette-live-grey-7);
			display: flex;
			font-size: 40px;
			font-weight: var(--font-weight-extra-light);
			height: 42px;
			justify-content: center;
			width: 42px;
			cursor: pointer;
		}
	}

	.stages-menu__popup-wrapper {
		margin: 0 16px 0 16px;

		.stages-menu__popup-description {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 0 20px 0;
			font-weight: var(--font-weight-regular);
			text-align: center;
		}
	}
}