.double-search-players {
	padding: 0 0 20px 0;
	border-bottom: 1px solid var(--palette-live-grey-7);

	&__input--first {
		margin: 0 16px 16px 16px;
	}
	
	&__input--second {
		margin: 0 16px 0 16px;
	}

	&__button-add {
		margin: 0 16px 0 16px;
	}
}

.double-search-players__line {
	background-color: var(--palette-live-grey-7);
	width: 100%;
	height: 1px;
	margin: 16px 0 0 0;
	display: block;
}

.double-search-players__list-item {
	align-items: center;
	border-bottom: 1px solid var(--palette-live-grey-7);
	cursor: pointer;
	display: flex;
	justify-content: space-between;
	padding: 16px 16px 16px 16px;

	.double-search-players__player-name {
		color: var(--palette-live-grey-0);
		font-size: var(--font-size-18);
		font-weight: var(--font-weight-regular);
		margin: 0 0 0 0;
	}

	.double-search-players__list-item-status {
		color: var(--palette-live-grey-0);
		font-size: var(--font-size-18);
		font-weight: var(--font-weight-regular);
		margin: 0 0 0 0;
	}
}