.stage-build {
	.double-button.fixed {
		bottom: 140px;
	}
}

.stage-build__level {
	margin: 0 16px 0 16px;

	&:first-child {
		.stage-build__level-wrapper {
			display: flex;
			align-items: center;
			margin: 15px 0;
		}
	}

	&-wrapper {
		display: flex;
		justify-content: space-between;
		margin: 25px 0 15px 0;

		.stage-build__level-name {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 0 0 0;
			font-weight: var(--font-weight-bold);
		}

		.delete-icon {
			width: 24px;
			height: 24px;
			fill: var(--palette-live-grey-6);
			cursor: pointer;
		}
	}
}

.stage-build__buttons-wrapper {
	display: flex;
	justify-content: space-between;
	margin: 16px 0 10px 0;
	flex-wrap: wrap;

	svg,
	span {
		cursor: pointer;
	}

	&--margin {
		margin: 16px 16px 16px 16px;
	}

	button:first-child {
		margin: 0 8px 0 0;
	}

	@media (max-width: 313px) {
		button:first-child {
			margin: 0 8px 8px 0;
		}
	}
}

.stage-build__delete-button {
	width: 100%;
	color: var(--palette-live-grey-7);
	font-size: var(--font-size-20);
	margin: 15px 0;
	padding: 0 0 0 0;
	border: 0;
	font-weight: 500;
	background-color: var(--palette-live-bg-1-white);
	cursor: pointer;
}

.stage-build__confirmation {
	color: var(--palette-live-grey-0);
	font-size: var(--font-size-15);
	margin: 0 16px 20px 16px;
	text-align: center;
}

.stage-build__confirmation-buttons {
	display: flex;
	justify-content: center;
	margin: 0 16px 0 16px;

	button:first-child {
		margin-right: 5px;
	}

	button:last-child {
		margin-left: 5px;
	}
}