.tournament-form {
	overflow: auto;
	margin: 0 16px 0 16px;

	.tournament-form__wrapper {
		margin: 16px 0 0 0;

		.tournament-form__description {
			color: var(--palette-live-grey-2);
			font-size: var(--font-size-18);
			margin: 0 0 16px 0;
			font-weight: var(--font-weight-regular);
		}

		.tournament-form__button {
			margin: 0 0 20px 0;
		}
	}
}