.stage-group {
	border-radius: 16px;
	border: 1px solid var(--palette-live-grey-7);
	margin: 0 0 8px 0;

	&:last-child {
		margin: 0 0 0 0;
	}

	&__header {
		display: flex;
		justify-content: space-between;
		padding: 14px 20px 14px 20px;

		&--border {
			border-bottom: 1px solid var(--palette-live-grey-11);
		}

		.stage-group__name-wrapper {
			display: flex;

			.stage-group__header-name-group {
				color: var(--palette-live-grey-0);
				font-size: var(--font-size-22);
				margin: 0 16px 0 0;
				font-weight: var(--font-weight-semi-bold);
			}
		}

		.group__header-wrapper {
			display: flex;
			align-items: center;

			.round-robin__icon, .olympic-plus__icon, .no-player__icon, .drop-out__icon, .olympic__icon, .olympic-three__icon {
				width: 24px;
				height: 24px;
				fill: var(--palette-live-grey-0);
			}

			.stage-group__header-counter {
				color: var(--palette-live-grey-0);
				font-size: var(--font-size-22);
				margin: 0 8px 0 8px;
				font-weight: var(--font-weight-semi-bold);
			}

			.arrow-down__icon {
				
			}
		}
	}
}

.stage-group__popup-participants-item {
	border: solid var(--palette-live-grey-9);
	border-top-width: 1px;
	border-bottom-width: 0;
	border-left-width: 0;
	border-right-width: 0;
	padding: 20px 16px;
	box-sizing: border-box;
	cursor: pointer;

	&--selected {
		background-color: #ebf4ff;
		cursor: pointer;
	}

	&.empty-node{
		display: flex;
		align-items: center;

		p {
			margin-right: 20px;
		}

		span {
			width: 55px;
			height: 1px;
			background-color: var(--palette-live-grey-0);
			display: block;
		}
	}

	&.single {
		min-height: 50px;
	}

	&.double {
		min-height: 70px;
		padding: 10px 16px;
	}

	.stage-group__popup-participants-name {
		color: var(--palette-live-grey-0);
		font-size: var(--font-size-18);
		margin: 0 0 0 0;
		font-weight: var(--font-weight-regular);
	}

	.stage-group__popup-participants-doubles {
		display: flex;
		align-items: center;

		.stage-group__popup-doubles-rating {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 16px 0 0;
			font-weight: var(--font-weight-regular);
		}

		.stage-group__popup-doubles-name {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 0 0 0;
			font-weight: var(--font-weight-regular);
		}

		.stage-group__popup-participants-name--first {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 0 10px 0;
			font-weight: var(--font-weight-regular);
		}

		.stage-group__popup-participants-name--second {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 0 0 0;
			font-weight: var(--font-weight-regular);
		}
	}
}