.error-link {
	margin: 30px 20px 0 20px;
	background-color: var(--palette-live-bg-0-white);
	padding: 16px 16px 16px 16px;
	border: 1px solid var(--palette-live-grey-5);
	border-radius: 16px;
	display: flex;

	&__icon-wrapper {
		width: 47px;
		height: 47px;
		background-color: var(--palette-live-red-2);
		border: 1px solid var(--palette-live-red-3);
		border-radius: 10px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		margin: 0 16px 0 0;

		.warning-icon {
			width: 23px;
			height: 23px;
			fill: var(--palette-live-bg-0-white);
			margin: 0 0 3px 0;
		}
	}

	&__specification {
		.error-link__title {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			font-weight: var(--font-weight-semi-bold);
			margin: 0 0 7px 0;
		}

		.error-link__description {
			color: var(--palette-live-grey-3);
			font-size: var(--font-size-16);
			font-weight: var(--font-weight-regular);
			margin: 0 0 0 0;
		}
	}
}